.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

/*********************************************/
/* GENERAL REUSABLE COMPONENTS */

.heading-primary {
  font-weight: 300;
  font-size: 6.4rem;
  line-height: 1.1;
  letter-spacing: -0.8px;
  color: #fff;
}
.heading-secondary {
  font-weight: 600;
  font-size: 4.8rem;
  line-height: 1.2;
  letter-spacing: -0.8px;
  color: #242b37;
  margin-bottom: 1.6rem;
}
.heading-tertiary {
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 1.67;
  letter-spacing: -0.22px;
}
.text,
.text-small,
.text-light {
  font-weight: 300;
}
.text,
.text-light {
  font-size: 2rem;
  line-height: 1.4;
}
.text-light {
  color: #ffffff7e;
}
.text-small {
  font-size: 1.5rem;
  line-height: 1.867;
  color: #ffffff;
}

.text-box {
  max-width: 38rem;
}
/* HELPER CLASSES*/
.t-white {
  color: #ffffff;
}
.t-main {
  color: #242b37;
}
.mb-medium {
  margin-bottom: 3.2rem;
}
.btn {
  width: 27rem;
  height: 5.8rem;
  display: inline-block;
  padding: 0.7rem 0;
  font-size: 1.5rem;
  text-align: center;
  transition: all 400ms ease-in;
  text-transform: uppercase;
  border-radius: 3px;
  text-decoration: none;
  cursor: pointer;
}
.btn-transparent {
  color: #ffffff;

  background-color: transparent;
  border: 1px solid #ffffff;
}
.btn-transparent:hover {
  color: #000000;
  background-color: #ffffff;
  border: 1px solid #ffffff;
}
.btn-full {
  color: #ffffff;
  background-color: #000000;
  border: 1px solid #000000;
}
.btn-full:hover {
  color: #000000;
  background-color: #ffffff;
  border: 1px solid #000000;
}
.form-btn {
  width: 27rem;
  height: 5.8rem;
  padding: 0.7rem 0;
  font-size: 1.5rem;
  text-align: center;
  transition: all 400ms ease-in;
  text-transform: uppercase;
  border-radius: 3px;
  cursor: pointer;
  color: #ffffff;
  background-color: #000000;
  border: 1px solid #000000;
  margin-top: 4.8rem;
}
.form-btn:hover {
  color: #000000;
  background-color: #ffffff;
  border: 1px solid #000000;
}
