.home {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.home-top {
  width: 100vw;
  height: 76rem;
}

/* section-1 */

.section-1 {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.section-1-cont {
  width: 100vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.img-box {
  display: flex;
  justify-content: center;
  box-shadow: 0 7.5rem 10rem -5rem #242b37;
}
.img-box-1 {
  width: 44rem;
  margin-left: 15%;
  margin-top: -10rem;
}
.img-box-2 {
  width: 44rem;
  margin-right: 15%;
}

.step {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 72rem;
  position: relative;
  margin-top: -10rem;
}

.pattern-divide {
  margin-bottom: 3.2rem;
}
.pattern-1,
.pattern-2 {
  position: absolute;
  height: 30rem;
  width: 65%;
  z-index: -1;
}
.pattern-1 {
  left: 0;
  bottom: 30%;
}
.pattern-2 {
  bottom: 20%;
  right: 0;
}
.step-img {
  max-width: 100%;
}

.hero {
  width: 100vw;
  height: 75rem;
  padding: 6.5rem 15%;
  display: flex;
  flex-direction: column;
  z-index: 1000;
}
.logo {
  width: 10.3rem;
  height: 4rem;
}
.hero-text-box {
  max-width: 44.5rem;
  font-weight: 300;
  margin-top: 14rem;
}
.hero-description {
  font-size: 2.4rem;
  color: #ffffff;
  margin-bottom: 2.4rem;
  margin-top: 2.4rem;
}

.hilights-ct {
  width: 100vw;
  height: 76rem;
  background-color: #111111;
  padding: 25rem 15% 12rem 15%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: -15rem;
}
.hilights-item-ct {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 54rem;
  border-bottom: 1px solid #000000;
  padding-bottom: 2.4rem;
  margin-bottom: 2.4rem;
}
.image-wrap {
  width: 12.8rem;
  height: 9.6rem;
  z-index: 100;
  position: relative;
}
.menu-item-img {
  width: 100%;
  height: 100%;
  z-index: 20;
}
.item-text-box {
  width: 32rem;
}
.item-pattern {
  position: absolute;
  top: 50%;
  left: 100%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: -1;
}

.text-box-left {
  margin-left: 15%;
}
.text-box-right {
  margin-right: 15%;
}
.pattern-hidden {
  visibility: hidden;
}

.events {
  width: 100vw;
  height: 92rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 15%;
  position: relative;
}

.event-img-ct {
  max-width: 54rem;
  height: 60rem;
  box-shadow: 0 7.5rem 10rem -5rem rgba(56, 66, 85, 0.5);
  z-index: 100;
  transition: 300ms ease-in-out;
}
.selected {
  display: block;
  cursor: pointer;
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
}

.events-list {
  list-style-type: none;
}
.events-list li {
  text-transform: uppercase;
  font-size: 1.6rem;
  line-height: 2rem;
  color: #5c6779c5;
  cursor: pointer;
  letter-spacing: 1px;
  position: relative;
}
.events-list li:hover,
.events-list li:active {
  color: #242b37;
}
.events-list li img {
  position: absolute;
  width: 10rem;
  left: -12rem;
  z-index: 90;
}
.event-text-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 2.4rem;
  gap: 2.4rem;
}
.events-btn-ct {
  display: flex;
  flex-direction: column;
  grid-gap: 2.4rem;
  gap: 2.4rem;
}
.events .pattern-1 {
  top: 0;
  position: absolute;
  z-index: 100;
  left: -40%;
}

.footer {
  display: block;
  width: 100%;
  background-color: #111111;
  margin-top: 5rem;
}
.footer-top {
  height: 24rem;
  padding: 7.8rem 16.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.footer-bottom {
  text-transform: uppercase;
  height: 24rem;
  padding: 7.8rem 16.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.navbar {
  width: 100%;
  background: #f9d401;

  z-index: 2000;
  position: relative;
}

.navigation {
  position: fixed;
  left: 1.6rem;
  height: 40%;
  background: #f9d401;
  width: 6%;
  box-shadow: 1px 0 2px rgba(56, 66, 85, 0.5);
  transition: 1s;
  border-radius: 1.5rem 5.5rem;
}

.navigation:hover {
  height: 95vh;
}

.navigation:hover nav {
  -webkit-transform: perspective(80vw) translateY(2vw) scaleY(1.2);
          transform: perspective(80vw) translateY(2vw) scaleY(1.2);
}
.navigation:hover nav ul li {
  margin-bottom: 5rem;
}
.navigation img {
  width: 15rem;
  height: 5rem;
  position: absolute;
  top: 5%;
}
.navigation:hover img {
  width: 20rem;
  height: 7rem;
  position: absolute;
  top: 5%;
}
nav {
  top: 30%;
  left: 5%;
  position: absolute;
  transition: 0.5s;
}

nav ul {
  color: white;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

nav ul li {
  line-height: 2;
  letter-spacing: 0.1em;
  margin-bottom: 1rem;
}

nav ul li a {
  font-size: 1.8rem;
  text-decoration: none;
  color: white;
  font-weight: 400;
}
nav ul li a:hover {
  color: #17192b;
}

.menu {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.menuList {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 2.4rem;
  gap: 2.4rem;
}

/* MENU ITEM */

.menuItem {
  border-radius: 1.5rem;
  width: 30rem;
  height: 24rem;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}
.menuItem:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease-in;
  cursor: pointer;
}

.menuItem div {
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  width: 100%;
  height: 18rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.menuItem h1,
.menuItem p {
  margin-left: 1rem;
  margin-top: 1rem;
  color: #5c6779;
  font-size: 1.8rem;
  font-weight: 400;
}
.menuItem p {
  color: rgba(56, 66, 85);
  font-weight: 500;
}

.contact {
  width: 100%;
  height: 100vh;
  display: flex;
}

.contact .leftSide {
  height: 100%;
  flex: 50% 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contact .rightSide {
  height: 100%;
  flex: 50% 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.contact .rightSide img {
  width: 20rem;
  height: 10rem;
  position: absolute;
  top: 2rem;
  right: 2ewm;
}
.contact .rightSide h1 {
  font-weight: 10;
  font-size: 5rem;
  margin-left: 3rem;
}

form {
  display: flex;
  flex-direction: column;
  width: auto;
  height: auto;
  padding: 3rem;
}

form input {
  height: 4rem;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
}

form textarea {
  margin-top: 1.5rem;
  height: 7rem;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  font-weight: bold;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
input::placeholder,
textarea::placeholder {
  font-weight: bold;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
form input:focus,
textarea:focus {
  outline: none;
}

form label {
  margin-top: 1.5rem;
  color: grey;
}

form button {
  margin-top: 4rem;
  width: 14rem;
  height: 4rem;
  border: none;
  background-color: #121619;
  color: whitesmoke;
  font-size: 1.5rem;
  cursor: pointer;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
*:focus {
  outline: none;
}
html {
  font-size: 62.5%;
  overflow-x: hidden;
}
body {
  font-family: "League Spartan", sans-serif;
  min-height: 100vh;
  margin: 0 auto;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-transition: background-color 5000s;
  transition: background-color 5000s;
  -webkit-text-fill-color: rgb(255, 255, 255) !important;
}

